.workWrapper{
    padding:25px;
    display: flex;
    flex-direction: column;
    justify-content:center ;
    align-items: center;
    background-color: #000;
    .wrapper{
        max-width: 1250px;
        margin: 0 auto;
        width: 100%;
        display: flex;
        flex-wrap:wrap;
        justify-content:center;
        margin-bottom: 30px;
        gap: 20px;

    }
}


     /*//Work*/
.card {
    width: 450px;
    margin: 10px;
    background-color: white;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
    @media (max-width: 580px){
        width: 400px;
    }
    @media (max-width: 450px){
        width: 300px;
    }
    &:hover .cardCaption {
        top: 50%;
        transform: translateY(-50%);
    }
    &:hover .cardImage {
        transform: translateY(-10px);
    }
    &:hover .cardImage {
        transform: translateY(-10px);
    }
    &:hover .cardThumb::after {
        top: 0;
    }
    &:hover .cardSnippet {
        margin: 20px 0;
    }
    .cardThumb {
        position: relative;
        display: flex;
        justify-content: center;
        max-height: 400px;
        overflow: hidden;
        &::after {
            position: absolute;
            top: 0;
            display: block;
            content: '';
            width: 100%;
            height: 100%;
            background: linear-gradient(0deg, rgba(0, 0, 0, .5) 40%, rgba(255, 255, 255, 0) 100%);
            transition: 0.3s;
        }
        .cardImage {
            transition: 1s ease-in-out;
            width: 450px;
            height: 300px;
        }
        .cardCaption {
            position: absolute;
            top: 50%;
            z-index: 1;
            padding: 0 20px;
            letter-spacing: 2px;

            color: white;
            transform: translateY(-50%);
            text-align: center;
            transition: 0.5s;
            .cardTitle {
                display: -webkit-box;
                max-height: 85px;
                overflow: hidden;
                font-size: 23px;
                line-height: 28px;
                text-shadow: 0px 1px 5px black;
                text-overflow: ellipsis;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
            }
            .cardSnippet {
                display: -webkit-box;
                max-height: 150px;
                margin: 20px 0;
                overflow: hidden;
                font-family: 'Roboto', sans-serif;
                font-size: 16px;
                line-height: 20px;
                text-overflow: ellipsis;
                transition: 0.5s ease-in-out;
                -webkit-line-clamp: 5;
                -webkit-box-orient: vertical;
            }
            .cardButton {
                display: inline-block;
                padding: 10px 20px;
                color: white;
                border: 1px solid white;
                font-family: 'Roboto', sans-serif;
                font-size: 12px;
                text-transform: uppercase;
                text-decoration: none;
                transition: 0.3s;
                &:hover {
                    color: black;
                    background-color: white;
                }
            }
        }
    }

}





@media (min-width: 1024px) {
    .cardThumb {
        max-height: 500px;
    }
}

@media (min-width: 1024px) {
    .cardThumb::after {
        top: calc(100% - 140px);
    }
}


@media (min-width: 1024px) {
    .cardCaption {
        top: calc(100% - 58px);
        transform: unset;
    }
}


@media (min-width: 1024px) {
    .cardSnippet {
        margin: 60px 0;
    }
}




@media (max-width:1300px) {
    .oneWork {
        min-width: 400px;
    }
}
@media (max-width:1015px){
    .wrapper{
        justify-content:center;

    }
}

@media (max-width:515px) {
    .img {
        max-width: 250px;
        min-height: 200px;
    }
    .oneWork {
        min-width: 280px;
        padding: 15px;
    }
}





